import { Component, h, Host, Prop, State } from '@stencil/core';

@Component({
    tag: 'ks-page-account-products',
    styleUrl: 'products.css'
})
export class PageAccountProducts{
    @Prop() heading: string = "Zamówione produkty";
    @Prop() accountLink: string;
    @Prop() accountButton: string = "Wróć do panelu klienta";
    @Prop() nodata: string = "Brak zamówionych produktów";

    @Prop() productsJson: string = "";
    @State() products: AccountProduct[];

    componentWillLoad() {
        this.products = this.productsJson ? JSON.parse(this.productsJson) : [];
    }

	render() {
		return <Host>
            <h1>{this.heading}</h1>

            {this.products.map(product => <a href={product.link} class="container">
                <div class="image">
                    <ks-img3 image={product.image} alt="zdjęcie produktu" fit="contain" aria-hidden />
                </div>
                <div class="name">{product.name}</div>
            </a>)}

            {this.products.length == 0 && <div class="nodata">{this.nodata}</div>}

            <ks-button rounded link={this.accountLink} name={this.accountButton} />
        </Host>;
	}
}


interface AccountProduct {
    link: string;
    image: string;
    name: string;
}