ks-page-account-products {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex: 1 1 auto;
    gap: 10px;
    padding: 40px 20px;
    
}

ks-page-account-products .nodata {
    background-color: white;
    border: #ccc 1px solid;
    border-radius: 10px;
    text-align: center;

    padding: 15px 30px;
    max-width: 100%;
    width: 700px;
    box-sizing: border-box;
}

ks-page-account-products .container {
    display: flex;
    align-items: center;
    gap: 10px;

    max-width: 100%;
    width: 700px;
    padding: 15px;

    background-color: white;
    color: black;
    border: #ccc 1px solid;
    border-radius: 10px;
    box-sizing: border-box;

    position: relative;
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.0);
    transition: box-shadow ease 0.2s;
}

ks-page-account-products .container:hover {
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    z-index: 1;
}

ks-page-account-products .container:active {
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
}

ks-page-account-products .image {
    max-height: 60px;
    max-width: 60px;
}


ks-page-account-products ks-button {
    margin-top: 10px;
}

ks-page-account-products h1 {
    text-align: center;
    margin-bottom: 10px;
}

@media (max-width: 420px) {
    ks-page-account-products .container {
        flex-direction: column;
        padding: 15px;
    }

    ks-page-account-products .image {
        max-height: 120px;
        max-width: 120px;
    }

    ks-page-account-products .name {
        text-align: center;
    }
}